.container {
        display: flex;
        justify-content: center; /* 수평 가운데 정렬 */
        align-items: center; /* 수직 가운데 정렬 */
        width: 100%;
        height: 100vh;
        background-image: url('back.jpg');
        
        /* 다른 스타일 속성 */
    
}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    width: 450px;
    height: 650px;
    padding: 20px 40px;
    background: rgba(255, 255, 255, 0.1); /* 투명한 유리 배경 */
    backdrop-filter: blur(10px); /* 블러 효과 */
    border: 1px solid rgba(255, 255, 255, 0.18); /* 테두리 스타일 */
    border-radius: 10px; /* 둥근 모서리 */
    color: #fff; /* 텍스트 색상 */
    text-align: center;
    font-weight: bold;
    font-size: 24px;
    cursor: pointer;
    transition: background 0.3s, transform 0.2s;
}

.close-btn {
    background-color: rgb(242, 129, 129);
    border: none;
    height: 15px;
    width: 15px;
    border-radius: 50%;
    position: absolute; /* 위치 지정을 위한 절대 위치 지정 */
    top: 20px; /* 상단 여백 */
    left: 20px; /* 좌측 여백 */
}

